.one-expense {
  padding: 0.5rem;
  border: 1px solid #e0e0e0;
  margin: 0.7rem 0;
  border-radius: 5px;
  background: #f3f3f3;

  display: flex;
  justify-content: space-between;

  .lnr {
    font-size: 1rem;
    text-decoration: none;
    color: #e47575;
    display: flex;
    align-items: center;

    &:hover {
      color: red;
    }
  }
}
